"use client"

import { SectionCards } from "@/components/section-cards";
import { ChartAreaInteractive } from "@/components/chart-area-interactive";
import { DataTable } from "@/components/data-table";
import { ColumnDef } from "@tanstack/react-table";
import { apiService } from "@/lib/api";
import { useEffect, useState } from "react";

//   
type Customer = {
  id: string;
  name: string;
  email: string;
  status: "" | " " | "";
  lastActive: string;
  subscription: string;
};

//    
const data: Customer[] = [
  {
    id: "1",
    name: " ",
    email: "ahmed@example.com",
    status: "",
    lastActive: "2025-07-09",
    subscription: " "
  },
  {
    id: "2",
    name: " ",
    email: "sara@example.com",
    status: "",
    lastActive: "2025-07-08",
    subscription: " "
  },
  {
    id: "3",
    name: " ",
    email: "mohamed@example.com",
    status: "",
    lastActive: "2025-06-30",
    subscription: " "
  },
  {
    id: "4",
    name: " ",
    email: "fatima@example.com",
    status: " ",
    lastActive: "2025-06-15",
    subscription: " "
  },
  {
    id: "5",
    name: " ",
    email: "khaled@example.com",
    status: "",
    lastActive: "2025-07-10",
    subscription: " "
  }
];

//   
const columns: ColumnDef<Customer, any>[] = [
  {
    accessorKey: "name",
    header: ""
  },
  {
    accessorKey: "email",
    header: " "
  },
  {
    accessorKey: "status",
    header: "",
    cell: ({ row }) => {
      const status = row.getValue("status") as string;
      return (
        <div className={`px-2 py-1 rounded-full text-center text-sm ${status === "" ? "bg-green-100 text-green-800" : status === "" ? "bg-yellow-100 text-yellow-800" : "bg-red-100 text-red-800"}`}>
          {status}
        </div>
      );
    }
  },
  {
    accessorKey: "lastActive",
    header: " "
  },
  {
    accessorKey: "subscription",
    header: " "
  }
];

export default function Page() {
  const [customers, setCustomers] = useState<Customer[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchCustomers = async () => {
      try {
        setLoading(true);
        const response = await apiService.getCustomers({ page: 1, limit: 10 });
        
        //    API    
        if (response.data && Array.isArray(response.data)) {
          const formattedCustomers = response.data.map(customer => {
            //        
            let status: "" | " " | "" = " ";
            if (customer.status === 'active') status = "";
            else if (customer.status === 'pending') status = "";
            
            return {
              id: customer.id?.toString() || '',
              name: customer.name || '',
              email: customer.email || '',
              status: status,
              lastActive: customer.lastActive || new Date().toISOString().split('T')[0],
              subscription: customer.subscription?.name || ' '
            };
          });
          setCustomers(formattedCustomers);
        } else {
          //        
          setCustomers(data);
        }
      } catch (err) {
        console.error('    :', err);
        setError('    ');
        //      
        setCustomers(data);
      } finally {
        setLoading(false);
      }
    };

    fetchCustomers();
  }, []);

  return (
    <div className="flex flex-1 flex-col">
      <div className="@container/main flex flex-1 flex-col gap-2">
        <div className="flex flex-col gap-4 py-4 md:gap-6 md:py-6">
          <div className="px-4 lg:px-6">
            <h1 className="text-3xl font-bold mb-4"> </h1>
          </div>
          <SectionCards />
          <div className="px-4 lg:px-6">
            <ChartAreaInteractive />
          </div>
          <div className="px-4 lg:px-6">
            <h2 className="text-xl font-semibold mb-4"> </h2>
            {error && <div className="text-red-500 mb-4">{error}</div>}
            {loading ? (
              <div className="text-center py-4">  ...</div>
            ) : (
              <DataTable columns={columns} data={customers} />
            )}
          </div>
        </div>
      </div>
    </div>
  );
}